import { useAnimation, motion } from 'framer-motion'
import React from 'react'
import { useInView } from 'react-intersection-observer'
import styles from '../Work.module.css'
import pizza from '../images/pizza.png'
import ButtonLink from '../../reused/ButtonLink'
import ReactPlayer from 'react-player'
import { pizza_video } from '../../../video/video'
import config from '../../../project_info'
import language from '../../../store/language'

const Pizza = () => {    
    let {ref, inView} = useInView() 
    let [videoBlock, setVideoBlock] = React.useState(false) 
    let animation = useAnimation()
    
    React.useEffect(() => {
        if (inView) {
            animation.start({
                x: 0,
                opacity: 1,
                transition: {duration: 1}
            })
        }
    })
    
    const open_git = (e: any): void => {
        if (document.body.clientWidth > 992) {
            if (e.target.classList.contains('_git-hub'))
                e.target.style.opacity = '1' 
        }
    };

    function close_git(e: any): void {
        if (document.body.clientWidth > 992) {
            if (e.target.classList.contains('_git-hub'))
                e.target.style.opacity = '0.3'
        }
    }
    
    return (
        <motion.div ref={ref} animate={animation} initial={{opacity: 0, x: 220}} className={styles.ready__site}>
            <div className={styles.gitHub__links}>
                <img src={pizza} width='140' height='140' alt="" />
                <div className='_git-hub' onMouseLeave={(e) => close_git(e)} onMouseEnter={(e) => open_git(e)} >    
                    <ButtonLink where={config[language.language].pizza.client_git}>GitHub client</ButtonLink>  
                    <br />
                    <ButtonLink where={config[language.language].pizza.server_git}>GitHub server</ButtonLink>  
                </div>
            </div>
            <div className={styles.info}>
                <div className={styles.main__info}>
                    <button onClick={(e) => setVideoBlock(!videoBlock)} className={styles.link + ' ' + styles.video__link}>{config[language.language].pizza.long_title}</button>
                    <p className={styles.description}>{config[language.language].pizza.description}</p>
                </div>
                <div>
                    <p className={styles.technology}><span>Технологии и инструменты: </span>{config[language.language].pizza.technology}</p>
                </div>
                {videoBlock && <div className={styles.video}>
                    <ReactPlayer light={pizza} controls width='500' height='360px'  url={pizza_video} />
                </div>}

            </div>
        </motion.div>
    )
}

export default Pizza
