import React from 'react'
import { filters } from './PortfolioWork'
import styles from './Option.module.css'
import { Text } from '../../text'
import language from '../../store/language'

const OptionFilter:React.FC<{filter: filters, setFilter: Function}> = ({ filter, setFilter }) => {


    return (
        <div className={styles.option__filter}>
            <div className={styles.square_1}></div>
            <div>
                <input type="checkbox" id="layout" onChange={() => setFilter((prev: any) => ({...prev, layout: !prev.layout}))} checked={filter.layout} />
                <label htmlFor="layout">{Text.filterLayout[language.language]}</label>
            </div>                
            <div>
                <input type="checkbox" id="cleanJS" onChange={() => setFilter((prev: any) => ({...prev, cleanJS: !prev.cleanJS}))} checked={filter.cleanJS} />
                <label htmlFor="cleanJS">{Text.filterJs[language.language]}</label>
            </div>               
            <div>
                <input type="checkbox" id="react" onChange={() => setFilter((prev: any) => ({...prev, react: !prev.react}))} checked={filter.react} />
                <label htmlFor="react">{Text.filterReact[language.language]}</label>
            </div>                
            <div>
                <input type="checkbox" id="angular" onChange={() => setFilter((prev: any) => ({...prev, angular: !prev.angular}))} checked={filter.angular} />
                <label htmlFor="angular">{Text.filterAngular[language.language]}</label>
            </div>                 
            <div>
                <input type="checkbox" id="fullstack" onChange={() => setFilter((prev: any) => ({...prev, fullstack: !prev.fullstack}))} checked={filter.fullstack} />
                <label htmlFor="fullstack">{Text.filterFullstack[language.language]}</label>
            </div>                 
            <div>
                <input type="checkbox" id="npmModule" onChange={() => setFilter((prev: any) => ({...prev, npmModule: !prev.npmModule}))} checked={filter.npmModule} />
                <label htmlFor="npmModule">{Text.filterNpm[language.language]}</label>
            </div> 
        </div>

    )

}

export default OptionFilter



                