import React from 'react'
import { motion } from 'framer-motion'
import Navlink from '../reused/NavLink'
import All_works from './All_works'
import Best_work from './Best_work'
import This_work from './This_work'
import styles from './Work.module.css'
import Library from './Library'
import AboutMe from './AboutMe'
import { Text } from '../../text'
import language from '../../store/language'

const Work = () => {
    React.useEffect(() => {
        document.documentElement.scrollTo({
            top: 0,
            left: 0
        })
    }, [])

    return (
        <motion.div initial={{opacity: 0}} animate={{opacity: 1}} transition={{duration: 0.7}} exit={{opacity: 0}} style={{ overflow: 'hidden'}}>
            <AboutMe />
            <Library />
            <Best_work />
            <This_work />
            <All_works />
            <div className={styles.center}> 
                <Navlink where='portfolio/'>{Text.allWorksButton[language.language]}</Navlink>
            </div>
        </motion.div>
    )
}

export default Work
