import React from 'react'
import styles from '../Work.module.css'  
import todo from '../images/Screenshot_1.png'
import { useInView } from 'react-intersection-observer'
import { motion, useAnimation } from 'framer-motion'
import ButtonLink from '../../reused/ButtonLink'
import SpecialLinks from '../SpecialLinks'
import SpecialInfo from '../SpecialInfo'
import config from '../../../project_info'
import language from '../../../store/language'

const Todo = () => {
    let {ref, inView} = useInView() 
    let animation = useAnimation()

    React.useEffect(() => {
        if (inView) {
            animation.start({
                x: 0,
                opacity: 1,
                transition: {duration: 1}
            })
        }
    })

    return (
        <motion.div style={{paddingBottom: '22px'}} ref={ref} animate={animation} initial={{opacity: 0, x: 220}} className={styles.ready__site}>
            <SpecialLinks photo={todo} size='140'>
                <ButtonLink where={config[language.language].todo.client_git}>GitHub</ButtonLink>
            </SpecialLinks>
            <SpecialInfo link={config[language.language].todo.link_site} title={config[language.language].todo.long_title} description={config[language.language].todo.description} technology={config[language.language].todo.technology} />
        </motion.div>
    )
}

export default Todo
