import React, { MouseEvent } from 'react'
import styles from '../Work.module.css'
import corona from '../images/Screenshot.jpg'
import { useInView } from 'react-intersection-observer'
import { motion, useAnimation } from 'framer-motion'
import ButtonLink from '../../reused/ButtonLink'
import SpecialLinks from '../SpecialLinks'
import SpecialInfo from '../SpecialInfo'
import config from '../../../project_info'
import language from '../../../store/language'

const Coronavirus = () => {
    let {ref, inView} = useInView() 
    let animation = useAnimation()
    
    React.useEffect(() => {
        if (inView) {
            animation.start({
                x: 0,
                opacity: 1,
                transition: {duration: 1}
            })
        }
    })

    return (
        <motion.div ref={ref} animate={animation} initial={{opacity: 0, x: 220}} className={styles.ready__site}>
            <SpecialLinks size='140' photo={corona}>
                <ButtonLink where={config[language.language].coronavirus.client_git}>GitHub</ButtonLink>
            </SpecialLinks>
            <SpecialInfo link={config[language.language].coronavirus.link_site} 
                        title={config[language.language].coronavirus.long_title} 
                        technology={config[language.language].coronavirus.technology} 
                        description={config[language.language].coronavirus.description} />
        </motion.div>
    )
}

export default Coronavirus
