import React from 'react'
import { filters } from './PortfolioWork'
import styles from './Option.module.css'
import OptionFilter from './OptionFilters'
import { Text } from '../../text'
import language from '../../store/language'

interface OptionsProps {
    setFirstOld: Function,
    firstOld: boolean
}


const OptionSort:React.FC<OptionsProps> = ({setFirstOld, firstOld}) => {


    return (
        <div className={styles.option__sort}>
            <div className={styles.square_2}></div>
            <div>
                <input type="checkbox" id="firstOld" onChange={() => setFirstOld(!firstOld)} checked={firstOld} />
                <label htmlFor="firstOld">{Text.firstOld[language.language]}</label>
            </div>
        </div>
    )

}

export default OptionSort



                