import React from 'react'
import styles from './Work.module.css'
import { motion, useAnimation } from 'framer-motion'
import {useInView} from 'react-intersection-observer'
import theme from '../../store/theme'
import { observer } from 'mobx-react-lite'

interface WorkTopProps {
    name: string,
    img: string
}

const Work_top:React.FC<WorkTopProps> = observer(({name, img}) => {
    let hrView = useInView()
    let textView = useInView()
    let iconView = useInView()
    let animation = useAnimation()
    let animation_2 = useAnimation()
    let animation_3 = useAnimation()

    React.useEffect(() => {       
        if (textView.inView) {
            animation_3.start({
                opacity: 1,
                transition: {delay: 1.3, duration: 0.6}
            })
        }       
    }, [textView.inView])

    React.useEffect(() => {       
        if (iconView.inView) {
            animation_2.start({
                opacity: 1,
                transition: {duration: 0.6}
            })
        }       
    }, [iconView.inView])

    React.useEffect(() => {       
        if (hrView.inView) {
            animation.start({
                width: '100%',
                transition: {duration: 1.5}
            })
        }       
    }, [hrView.inView])

    return (
        <div className={styles.work__title}>
            <motion.div initial={{opacity: 0}} ref={iconView.ref} animate={animation_2} className={styles.work__title__column}>{name}</motion.div>
            <div  className={styles.work__title__column}> <motion.hr initial={{width: '0%'}} ref={hrView.ref}  animate={animation} className={styles.hr}/> </div>
            <motion.div initial={{opacity: 0}} style={{backgroundColor: theme.black ? 'rgb(78, 95, 131)' : 'rgb(64, 151, 243)'}} ref={textView.ref} animate={animation_3} className={styles.work__title__column}><img src={img} alt=''  />  </motion.div>
        </div>
    )   
})

export default Work_top
