import React from 'react'
import styles from './Work.module.css'

interface SpecialLinksProps {
    photo: string,
    size: string
}

const SpecialLinks:React.FC<SpecialLinksProps> = ({ photo, children, size }) => {
    const open_git = (e: any): void => {   
        if (document.body.clientWidth > 992) {
            if (e.target.classList.contains('_git-hub'))
                e.target.style.opacity = '1'
        }
    };

    function close_git(e: any): void {
        if (document.body.clientWidth > 992) {
            if (e.target.classList.contains('_git-hub'))
                e.target.style.opacity = '0.3'
        }
    }

    return (
        <div className={styles.gitHub__links}>
            <img src={photo} width={size} height={size} alt="" />
            <div onMouseLeave={(e) => close_git(e)} style={{width: size + 'px', height: size + 'px'}} onMouseEnter={(e) => open_git(e)} className={styles.height + ' _git-hub'}>
                {children}
            </div>
        </div>
    )
}

export default SpecialLinks
