import React from 'react'
import Work_top from './Work_top'
import bestwork from './images/bestwork.png'
import instagram from './images/instagram.png'
import ButtonLink from '../reused/ButtonLink'
import config from '../../project_info'
import ReactPlayer from 'react-player'
import { useAnimation, motion } from 'framer-motion'
import { useInView } from 'react-intersection-observer'
import styles from './Work.module.css'
import { last_inst } from '../../video/video'
import { Text } from '../../text'
import language from '../../store/language'

const Best_work = () => {
    let {ref, inView} = useInView()
    let [videoBlock, setVideoBlock] = React.useState(false) 
    let animation = useAnimation()
    
    React.useEffect(() => {
        if (inView) {
            animation.start({
                x: 0,
                opacity: 1,
                transition: {duration: 1}
            })
        }
    })
    
    const open_git = (e: any): void => {
        if (document.body.clientWidth > 992) {
            if (e.target.classList.contains('_git-hub'))
                e.target.style.opacity = '1' 
        }
    };

    function close_git(e: any): void {
        if (document.body.clientWidth > 992) {
            if (e.target.classList.contains('_git-hub'))
                e.target.style.opacity = '0.3'
        }
    }

    return (
        <div>
            <Work_top name={Text.bigProject[language.language]} img={bestwork}/>
            <motion.div ref={ref} animate={animation} initial={{opacity: 0, x: 220}} className={styles.ready__site}>
                <div className={styles.gitHub__links + ' ' + styles.gitHub__links_big}>
                    <img src={instagram} width='170' height='170' alt="" />
                    <div className='_git-hub' onMouseLeave={(e) => close_git(e)} onMouseEnter={(e) => open_git(e)} >    
                        <ButtonLink where={config[language.language].instagram.client_git}>GitHub client</ButtonLink>  
                        <br />
                        <ButtonLink where={config[language.language].instagram.server_git}>GitHub server</ButtonLink>  
                    </div>
                </div>
                <div className={styles.info}>
                    <div className={styles.main__info}>
                        <button onClick={(e) => setVideoBlock(!videoBlock)} className={styles.link + ' ' + styles.video__link}>{config[language.language].instagram.short_title}</button>
                        <p className={styles.description}>{config[language.language].instagram.description}</p>
                    </div>
                    <div>
                        <p className={styles.technology}><span>{Text.tools[language.language]}: </span>{config[language.language].instagram.technology}</p>
                    </div>
                    {videoBlock && <div className={styles.video}>
                        <ReactPlayer light={instagram} controls width='500' height='360px'  url={last_inst} />
                    </div>}
                </div>
            </motion.div>
        </div>
    )
}

export default Best_work
