import React from 'react'
import { motion } from 'framer-motion'
import Navlink from '../reused/NavLink'
import styles from './PortfolioWork.module.css'
import TheWorkWrapper from './TheWorkWrapper'
import black_back from './images/black_back.png'
import white_back from './images/white_back.png'
import { NavLink } from 'react-router-dom'
import theme from '../../store/theme'
import { observer } from 'mobx-react-lite'
import OptionWripper from './OptionWripper'
import option_black from './images/option_black.png'
import option_white from './images/option_white.png'
import { Text } from '../../text'
import language from '../../store/language'

export interface filters {
    layout: boolean,
    cleanJS: boolean,
    react: boolean,
    angular: boolean,
    fullstack: boolean,
    npmModule: boolean
}

const PortfolioWork = observer(() => {
    let [firstOld, setFirstOld] = React.useState<boolean>(false) 
    let [filter, setFilter] = React.useState<filters>({
        layout: false,
        cleanJS: false,
        react: false,
        angular: false,
        fullstack: false,
        npmModule: false,
    })
    let [option, setOption] = React.useState<boolean>(false)

    React.useEffect(() => {
        document.documentElement.scrollTo({
            top: 0,
            left: 0
        })
    }, [])
    
    return (
        <motion.div initial={{opacity: 0}} style={{marginTop: '25px'}} transition={{duration: 0.7}} animate={{opacity: 1}} exit={{opacity: 0}}>
            <div className={styles.portfolio__row}>
                <div className={styles.portfolio__row}>
                    <NavLink to='/'><img src={theme.black ? white_back : black_back} alt="" width='30' height='30' /></NavLink>
                    <h2>{Text.portfolio[language.language]}</h2>  
                </div>
                <img onClick={() => setOption(!option)} width='30' height='30' src={theme.black ? option_white : option_black} alt=""/>
                {option && <OptionWripper setOption={setOption} filter={filter} setFirstOld={setFirstOld} setFilter={setFilter} firstOld={firstOld}/>}
            </div>
            <TheWorkWrapper filter={filter} firstOld={firstOld}/>
            <div className={styles.center}><Navlink where='/'>{Text.back[language.language]}</Navlink></div>
        </motion.div>
    )
})

export default PortfolioWork
