import React from 'react'
import Work_top from './Work_top'
import ButtonLink from '../reused/ButtonLink'
import config from '../../project_info'
import Slider from './images/Screenshot_6.png'
import thiswork from './images/thiswork.png'
import { useAnimation } from 'framer-motion'
import { useInView } from 'react-intersection-observer'
import SpecialWrapper from './SpecialWrapper'
import SpecialLinks from './SpecialLinks'
import SpecialInfo from './SpecialInfo'
import { Text } from '../../text'
import language from '../../store/language'

const Library = () => {
    let {ref, inView} = useInView()
    let animation = useAnimation()
    
    React.useEffect(() => {
        if (inView) {
            animation.start({
                x: 0,
                opacity: 1,
                transition: {duration: 1}
            })
        }
    })

    return (
        <div>
            <Work_top name={Text.npmModule[language.language]} img={thiswork}/> 
            <SpecialWrapper>
                <SpecialLinks size='170' photo={Slider} >
                    <ButtonLink where={config[language.language].slider_range.client_git}>GitHub</ButtonLink>
                </SpecialLinks>
                <SpecialInfo link={config[language.language].slider_range.link_site} title={config[language.language].slider_range.short_title} description={config[language.language].slider_range.description} technology={config[language.language].slider_range.technology} />
            </SpecialWrapper>      
        </div>
    )
}

export default Library
