import React, { useContext } from 'react'
import { Context } from '../..'
import { useCollectionData } from 'react-firebase-hooks/firestore'
import user from './images/user.jpg'
import { motion } from 'framer-motion'
import styles from './Review.module.css'
import { count_time } from '../../time'
import Navlink from '../reused/NavLink'
import loading_icon from './images/loading_icon.gif'
import { Text } from '../../text'
import language from '../../store/language'

interface timeProps {
    seconds: number,
    nanoseconds: number,
    toDate: () => string
}

interface commentsProps {
    name: string,
    text: string,
    time: timeProps,

}

const Review = () => {
    let  { firestore } = useContext(Context)
    let [ comments, loading ] = useCollectionData<commentsProps>(firestore.collection('comment').orderBy('time', 'desc'))
    let [ comment, setComment ] = React.useState<Array<commentsProps>>()
    
    React.useEffect(() => {
        document.documentElement.scrollTo({
            behavior: 'smooth',
            top: 0,
            left: 0
        }) 
    }, [])

    React.useEffect(() => {
        if (comments) setComment(comments);
    }, [comments])

    let count_comment: string | undefined = comment?.length.toString()

    if (loading) return <div className={styles.loading}><img src={loading_icon} width='50' height='50' alt="" /></div>

    return (
        <motion.div initial={{opacity: 0}} transition={{duration: 0.7}} animate={{opacity: 1}} exit={{opacity: 0}}>
           <h2>{Text.reviewLabel[language.language]}:  {count_comment}</h2>
            {comment && (
               <>
                {comment.map((item, index) => {
                    let time: string;
                    item.time ? time = count_time(item.time.seconds * 1000) : time = 'Опубликовано менее часа назад'                        
                    return <div key={item.text} className={styles.comment__grid + ' ' + (index === 0 && styles.first__comment)}>
                                <div className={styles.comment__img}><img src={user} width='60' height='60' alt="" /></div>
                                <div className={styles.comment__info}>
                                    <span>{item.name}</span>
                                    <span>{time}</span>
                                    <div>
                                        <p>{item.text}</p>
                                    </div>
                                </div>
                            </div>
                })}
               </> 

            )}
            <div className={styles.center}><Navlink where='/'>{Text.back[language.language]}</Navlink></div>
        </motion.div>
    )
}

export default Review
