import React from 'react'
import language from '../../store/language'
import { Text } from '../../text'
import styles from './Work.module.css'

interface SpecialInfoProps {
    link: string,
    title: string,
    description: string,
    technology: string
}

const SpecialInfo:React.FC<SpecialInfoProps> = ({ link, title, description, technology }) => {
    return (
        <div className={styles.info}>
            <div>
                <a href={link} target="blank" className={styles.link}>{title}</a>
                <p className={styles.description}>{description}</p>
            </div>
            <div>
                <p className={styles.technology}><span>{Text.tools[language.language]}: </span>{technology}</p>
            </div>
        </div>
    )
}

export default SpecialInfo
