import moment from 'moment'

export function count_time(publish_time: number):string {
    if (moment().diff(publish_time, 'hour') === 0) return `Опубликовано менее часа назад`
    if (moment().diff(publish_time, 'hour') < 24){
        let hour: string;
        if (moment().diff(publish_time, 'hour') === 1 || 
            moment().diff(publish_time, 'hour') === 21) {
                hour = 'час'
        }
        else if (moment().diff(publish_time, 'hour') === 2 || 
                moment().diff(publish_time, 'hour') === 3 ||
                moment().diff(publish_time, 'hour') === 4 ||
                moment().diff(publish_time, 'hour') === 22 ||
                moment().diff(publish_time, 'hour') === 23) {
                    hour = 'часа'
        }
        else hour = 'часов'
        return `Опубликовано ${moment().diff(publish_time, 'hour')} ${hour} назад`
    }
    else if (moment().diff(publish_time, 'day') < 31) {
        let day: string;
        if (moment().diff(publish_time, 'day') === 1 || 
            moment().diff(publish_time, 'day') === 21 || 
            moment().diff(publish_time, 'day') === 31) {
                day = 'день'
        }
        else if (moment().diff(publish_time, 'day') === 2 || 
                moment().diff(publish_time, 'day') === 3 ||
                moment().diff(publish_time, 'day') === 4 ||
                moment().diff(publish_time, 'day') === 22 ||
                moment().diff(publish_time, 'day') === 23 ||
                moment().diff(publish_time, 'day') === 24) {
                    day = 'дня'
        }
        else day = 'дней'
        return `Опубликовано ${moment().diff(publish_time, 'day')} ${day} назад`
    }
    else if (moment().diff(publish_time, 'month') < 12) {
        let month: string;
        if (moment().diff(publish_time, 'month') === 1) {
                month = 'месяц'
        }
        else if (moment().diff(publish_time, 'month') === 2 || 
                moment().diff(publish_time, 'month') === 3 ||
                moment().diff(publish_time, 'month') === 4) {
                    month = 'месяца'
        }
        else month = 'месяцев'
        return `Опубликовано ${moment().diff(publish_time, 'month')} ${month} назад`
    }
    else {
        let year: string;
        if (moment().diff(publish_time, 'year') === 1 || 
            moment().diff(publish_time, 'year') === 21 ||
            moment().diff(publish_time, 'year') === 31) {
                year = 'год'
        }
        else if (moment().diff(publish_time, 'year') === 2 || 
                moment().diff(publish_time, 'year') === 3 ||
                moment().diff(publish_time, 'year') === 4 ||
                moment().diff(publish_time, 'year') === 22 ||
                moment().diff(publish_time, 'year') === 23 ||
                moment().diff(publish_time, 'year') === 24 || 
                moment().diff(publish_time, 'year') === 32 || 
                moment().diff(publish_time, 'year') === 33 || 
                moment().diff(publish_time, 'year') === 34) {
                    year = 'года'
        }
        else year = 'лет'
        return `Опубликовано ${moment().diff(publish_time, 'year')} ${year} назад`
    }
}