import React from 'react'
import styles from './Contact.module.css'
import mail from './images/mail.png'
import phone from './images/phone.png'
import place from './images/place.png'
import Alerts from '../reused/Alerts'
import { AnimatePresence } from 'framer-motion'
import Contact_form from './Contact_form'
import ButtonLink from '../reused/ButtonLink'
import theme from '../../store/theme'
import { observer } from 'mobx-react-lite'
import { Text } from '../../text'
import language from '../../store/language'

const Contact:React.FC = observer(() => {
    let [name, setName] = React.useState<string>('')
    let [text, setText] = React.useState<string>('')
    let [modal, setModal] = React.useState<boolean>(false)

    return (
        <div style={{backgroundColor: theme.black ? 'rgb(78, 95, 131)' : 'rgb(64, 151, 243)'}} className={styles.footer}>
            <AnimatePresence> 
                {modal && <Alerts text={Text.thanks[language.language]} />}
            </AnimatePresence>
            <div className={styles.contact}>
                <div><img src={mail} width='18' height='15' alt="" />fuad.fuad.1984@list.ru</div>
                <div><img src={phone} width='18' height='18' alt="" />+994552402362</div>
                <div><img src={place} width='18' height='18' alt="" />Azerbaycan / Baku</div>
                <div className={styles.link}>
                    <ButtonLink where='https://drive.google.com/file/d/1U6gqZZJ084XxZLRMlUSfBScIArYFVgR4/view'>
                        {Text.download[language.language]}
                    </ButtonLink>
                </div>
            </div>
            <Contact_form setModal={setModal} name={name} text={text} setName={setName} setText={setText} />
        </div>
    )
})

export default Contact
