import React from 'react'
import styles from '../Work.module.css'  
import animation_img from '../images/Screenshot_32.png'
import { useInView } from 'react-intersection-observer'
import { motion, useAnimation } from 'framer-motion'
import ButtonLink from '../../reused/ButtonLink'
import SpecialLinks from '../SpecialLinks'
import SpecialInfo from '../SpecialInfo'
import config from '../../../project_info'
import language from '../../../store/language'

const SqrLearning = () => {
    let {ref, inView} = useInView() 
    let animation = useAnimation()
    
    React.useEffect(() => {
        if (inView) {
            animation.start({
                x: 0,
                opacity: 1,
                transition: {duration: 1}
            })
        }
    })

    return (
        <motion.div ref={ref} animate={animation} initial={{opacity: 0, x: 220}} className={styles.ready__site}>
            <SpecialLinks photo={animation_img} size='140'>
                <ButtonLink where={config[language.language].sqrLearning.client_git}>GitHub</ButtonLink>
            </SpecialLinks>
            <SpecialInfo link={config[language.language].sqrLearning.link_site} title={config[language.language].sqrLearning.long_title} description={config[language.language].sqrLearning.description} technology={config[language.language].sqrLearning.technology} />
        </motion.div>
    )
}

export default SqrLearning
