import React, { useEffect } from 'react'
import styles from './Header.module.css'
import avatar from './images/avatar.jpg'
import {motion} from 'framer-motion'
import arrow from './images/arrow.png'
import arrow_white from './images/arrow_white.png'
import black_set from './images/black_set.png'
import white_set from './images/white_set.png'
import theme from '../../store/theme'
import { observer } from 'mobx-react-lite'
import Menu from './Menu'
import { Text } from '../../text'
import language from '../../store/language'

const Header:React.FC = observer(() => {
    let [open, setOpen] = React.useState<boolean>(false)

    useEffect(() => {
        if (localStorage.getItem('color') === 'dark') {
            theme.changeTheme(true)
            document.body.style.background = 'black'
            document.body.style.color = 'white'
        } 
    
        else if (localStorage.getItem('color') === 'light') {
            document.body.style.background = 'white'
            document.body.style.color = 'black'
            theme.changeTheme(false)
        }
    }, [])

    return (
        <div className={styles.header} >
            <motion.div initial={{ opacity: 0 }} animate={{opacity: 1}} transition={{duration: 1}} className={styles.header__image_wrapper}>
                <motion.img initial={{ x: '-200px', y: '-50%', opacity: 0 }} transition={{duration: 1}} animate={{ x: '-50%', y: '-50%', opacity: 1 }} className={styles.header__image} src={avatar} width="120" height='120' alt="" />
            </motion.div> 
            <div className={styles.name__wrapper}>
                <motion.p initial={{y: '-20px', opacity: 0}} transition={{duration: 0.5}} animate={{y: 0, opacity: 1}} className={styles.name}>
                    {Text.name[language.language]}
                </motion.p>
                <motion.p initial={{y: '-20px', opacity: 0}} transition={{delay: 0.5, duration: 0.5}} animate={{y: 0, opacity: 1}} style={{color: theme.black ? 'rgb(78, 95, 131)' : 'rgb(64, 151, 243)'}} className={styles.lastname}>
                    {Text.lastname[language.language]}
                </motion.p>
            </div>
            <div className={styles.header__column}>
                <div className={styles.header__label}>
                    <motion.div initial={{y: '-20px', opacity: 0}} animate={{y: '-0px', opacity: 1}} transition={{delay: 0.7, duration: 0.5}} ><img className={styles.margin_icon} src={theme.black ? arrow_white : arrow} width='13' height='16'/> </motion.div>
                    <motion.div initial={{y: '-20px', opacity: 0}} transition={{duration: 0.5}} animate={{y: 0, opacity: 1}} className={styles.job}>
                        {Text.job[language.language]}
                    </motion.div>
                </div>
                <img onClick={() => setOpen(!open)} className={styles.header__setting}  src={theme.black ? white_set : black_set} alt="" width='30' height='30' />
                {open && <Menu setOpen={setOpen} />}
            </div>
        </div>
    )
})

export default Header