import React from 'react'
import ReactPlayer from 'react-player'
import styles from './Video_component.module.css'

interface VideoProps {
    path: string | undefined,
    toClose: () => void
}

const Video:React.FC<VideoProps> = ({ path = '#', toClose }) => {
    return (
        <div className={styles.video__wrapper}>
            <button className={styles.close__button} onClick={toClose}>X</button>
            <div className={styles.video}>
            <video controls className={styles.video_tag} >
                <source src={path} type="video/mp4" />
            </video>
            </div>
        </div>
    )
}

export default Video
