import React from 'react'
import { filters } from './PortfolioWork'
import styles from './Option.module.css'
import OptionFilter from './OptionFilters'
import OptionSort from './OptionSort'
import { Text } from '../../text'
import language from '../../store/language'

interface OptionsProps {
    filter: filters,
    setFilter: Function,
    setFirstOld: Function,
    firstOld: boolean,
    setOption: Function
}

const OptionWripper:React.FC<OptionsProps> = ({ filter, setFilter, firstOld, setFirstOld, setOption }) => {
    let [openFilter, setOpenFilter] = React.useState<boolean>(false)
    let [opensort, setOpenSort] = React.useState<boolean>(false)

    React.useEffect(() => {
        window.addEventListener('click', listenerOption)

        return () => {
            window.removeEventListener('click', listenerOption)
        }
    }, [])

    function listenerOption(e: any) {
        if (!e.target.closest("."+styles.option)) {
            setOption(false)
        }
    }
    
    function OpenFilterFunction(e: any) {
        if (e.target.closest('.'+styles.option__filter_wrapper)) {
            setOpenFilter(true)
        }  
    }

    function OpenSortFunction(e: any) {
        if (e.target.closest('.'+styles.option__filter_wrapper)) {
            setOpenSort(true)
        }  
    }
    
    return (
        <>
            <div className={styles.option}>
                <div className={styles.square}></div>
                <div onMouseMove={OpenFilterFunction} onMouseLeave={() => setOpenFilter(false)} className={styles.option__filter_wrapper}>
                    <p>{Text.filter[language.language]}</p>
                    {openFilter && <OptionFilter filter={filter} setFilter={setFilter} />}
                </div>
                <div onMouseMove={OpenSortFunction} onMouseLeave={() => setOpenSort(false)} className={styles.option__filter_wrapper}>
                    <p>{Text.sort[language.language]}</p>
                    {opensort && <OptionSort setFirstOld={setFirstOld} firstOld={firstOld}/>}
                </div>
            </div>
        </>
    )
}

export default OptionWripper
       