import { motion, useAnimation } from 'framer-motion'
import React from 'react'
import { useInView } from 'react-intersection-observer'
import styles from './Work.module.css'

interface WorkBarProps {
    name: string
}

const Work_bar:React.FC<WorkBarProps> = ({name, children}) => {
    let {ref, inView} = useInView()
    let animation = useAnimation()

    React.useEffect(() => {
        if (inView) {
            animation.start({
                x: 0,
                opacity: 1
            })
        }
    }, [inView])

    return (
        <>
            <motion.div ref={ref} initial={{x: 100, opacity: 0}} transition={{duration: 1}} animate={animation} className={styles.title}>{name}</motion.div>
            <div className={styles.work}>
                <div className={styles.work__column}>
                    <span>{name}</span>
                </div>
           
                <div className={styles.work__column}>
                    {children}
                </div>
            </div>
        </>
    )
}

export default Work_bar
