import React, { useEffect } from 'react'
import styles from './Header.module.css'
import theme from '../../store/theme'
import { observer } from 'mobx-react-lite'
import { Text } from '../../text'
import language from '../../store/language'

const Menu:React.FC<{setOpen: Function}> = observer(({ setOpen }) => {

    let ruInput = React.useRef<any>()
    let enInput = React.useRef<any>()

    function listenerMenu(e: any) {
        if (!e.target.closest("."+styles.header__menu)) {
            setOpen(false)
        }
    }

    useEffect(() => {
        window.addEventListener('click', listenerMenu)

        return () => {
            window.removeEventListener('click', listenerMenu)
        }
    }, [])

    useEffect(() => {
        let hash = language.language

        if (hash == 'ru') {
            ruInput.current!.checked = true
            enInput.current!.checked = false
        }

        else if (hash == 'en') {
            ruInput.current!.checked = false
            enInput.current!.checked = true
        }
    }, [window.location.hash])
    
    function changeColor() {
        if (!theme.black) {
            document.body.style.background = 'black'
            document.body.style.color = 'white'
            localStorage.setItem('color', 'dark')
            theme.changeTheme(true)
        }

        else if (theme.black) {
            document.body.style.background = 'white'
            document.body.style.color = 'black'
            localStorage.setItem('color', 'light')
            theme.changeTheme(false)
        }
    }

    function changeLanguage(e: any) {
        language.changeLanguage(e.target.id)
        window.localStorage.setItem('language', e.target.id)
        window.location.reload()
    }

    return (
        <div className={styles.header__menu}>
            <div className={styles.square}></div>
            <div>
                <input onChange={() => changeColor()} checked={theme.black} type="checkbox" id="theme" />
                <label htmlFor="theme">{Text.theme[language.language]}</label>
            </div>
            <div className={styles.header__language}>
                <div>
                    <input ref={ruInput} onChange={changeLanguage} type="radio" name="language" id='ru' />
                    <label htmlFor="ru">Ru</label>
                </div>
                <div>
                    <input ref={enInput} onChange={changeLanguage} type="radio" name="language" id='en' />
                    <label htmlFor="en">En</label>
                </div>
            </div>
        </div>
    )
})

export default Menu