import React from 'react'
import styles from '../Work.module.css'
import business_img from '../images/Screenshot_1.jpg'
import { useInView } from 'react-intersection-observer'
import { motion, useAnimation } from 'framer-motion'
import ButtonLink from '../../reused/ButtonLink'
import SpecialLinks from '../SpecialLinks'
import SpecialInfo from '../SpecialInfo'
import config from '../../../project_info'
import language from '../../../store/language'

const Business = () => {
    let {ref, inView} = useInView() 
    let animation = useAnimation()
    let config_copy: any = config[language.language]
    
    React.useEffect(() => {
        if (inView) {
            animation.start({
                x: 0,
                opacity: 1,
                transition: {duration: 1}
            })
        }
    })

    return (
        <motion.div ref={ref} animate={animation} initial={{opacity: 0, x: 220}} style={{paddingBottom: '22px'}} className={styles.ready__site}>
            <SpecialLinks photo={business_img} size='140'>
                <ButtonLink where={config_copy.business.client_git}>GitHub</ButtonLink>
            </SpecialLinks>
            <SpecialInfo link={config_copy.business.link_site} title={config_copy.business.long_title} description={config_copy.business.description} technology={config_copy.business.technology} />
        </motion.div>
    )
}

export default Business
