import { motion } from 'framer-motion'
import React from 'react'
import styles from './Alert.module.css'

interface AlertProps {
    text: string
}

const Alerts:React.FC<AlertProps> = ({text}) => {
    return (
            <motion.div  initial={{x: 100, opacity: 0}} animate={{x: 0, opacity: 1}} transition={{duration: 0.4}} exit={{x: 400, opacity: 0}} className={styles.body}>
                <p>{text}</p>
            </motion.div>
    )
}

export default Alerts
