import React from 'react'
import TheWork from './TheWork'
import styles from './TheWorkWrapper.module.css'
import config from '../../project_info'
import { TYPES } from '../../project_info'
import language from '../../store/language'

const TheWorkWrapper = ({firstOld, filter}) => {
    let data = []
    let config_copy = config[language.language]

    for (let key in config_copy) {
        data.push(config_copy[key])
    }

    if (!firstOld) {
        data = data.reverse()
    }

    function getKey(value) {
        for (let key in TYPES) {
            if (TYPES[key] === value) {
                return key
            }
        }
        return ''
    }

    function checkFilter() {
        for (let key in filter) {   
            if (filter[key]) {
                console.log(filter[key]);
                return true
            }
        }
        return false
    }

    return (
        <>
            {!checkFilter() ? 
                (
                    <div className={styles.grid}>
                        {data.map(item => {
                            return  <TheWork title={item.short_title} 
                                        client={item.client_git} 
                                        link={item.link_site} 
                                        image={item.image} 
                                        technology={item.technology}/>
                        })}
                    </div>  
                )

                : 

                (
                    <div className={styles.grid}>
                        {data.map(item => {
                            if (filter[getKey(item.type)]) {
                                return  <TheWork title={item.short_title} 
                                            client={item.client_git} 
                                            link={item.link_site} 
                                            image={item.image} 
                                            technology={item.technology}/>
                            }
                        })}
                    </div>  
                )
            }
        </>
    )
}

export default TheWorkWrapper
