import React from 'react'
import { motion } from 'framer-motion'
import styles from './Work.module.css'
import { Text } from '../../text'
import language from '../../store/language'

const AboutMe = () => {
    return (
        <>
            <motion.div initial={{y: '-20px', opacity: 0}} transition={{delay: 0.3, duration: 0.5}} animate={{y: '-0px', opacity: 1}} className={styles.header__text}>
                {Text.aboutMe[language.language]}
            </motion.div>
            <motion.div initial={{y: '-20px', opacity: 0}} transition={{delay: 0.5, duration: 0.5}} animate={{y: '-0px', opacity: 1}} className={styles.header__text}>
                {Text.aboutTechnology[language.language]}
            </motion.div>
            <motion.div initial={{y: '-20px', opacity: 0}} transition={{delay: 0.7, duration: 0.5}} animate={{y: '-0px', opacity: 1}} className={styles.header__text}>
                {Text.aboutProblem[language.language]}
            </motion.div>
        </>
    )
}

export default AboutMe
