import React, { Dispatch, FormEvent, SetStateAction, useContext } from 'react'
import Navlink from '../reused/NavLink'
import Button from '../reused/Button'
import styles from './Contact.module.css'
import { Context } from '../..'
import { Text } from '../../text'
import language from '../../store/language'

interface ContactFormProps {
    setModal: Dispatch<SetStateAction<boolean>>,
    setText: Dispatch<SetStateAction<string>>,
    setName: Dispatch<SetStateAction<string>>,
    name: string,
    text: string,
}

const Contact_form:React.FC<ContactFormProps> = ({ setModal, name, text, setText, setName }) => {
    let  {firestore, firebase } = useContext(Context)

    async function send_review(e: FormEvent) {
        e.preventDefault()
        if (name && text) {
            await firestore.collection('comment').add({
                name,
                text,
                time: firebase.firestore.FieldValue.serverTimestamp()
            })
            setName('')
            setText('')
            setModal(() => {
                setTimeout(() => setModal(false), 2000)
                return true
            })
        }
    }

    return (
        <form className={styles.review} onSubmit={(e) => send_review(e)}>
            <p>{Text.review[language.language]}</p>
            <div>
                <label htmlFor="name">{Text.enterName[language.language]}</label>
                <input type="text" value={name} onChange={(e) => setName(e.target.value)} id='name' placeholder={Text.namePlaceholder[language.language]}/>
            </div>
            <div>
                <label htmlFor="text">{Text.enterReview[language.language]}</label>
                <textarea id='text' value={text} onChange={(e) => setText(e.target.value)} placeholder={Text.reviewPlaceholder[language.language]}/>
            </div>
            <div className={styles.button_2}>
                <Navlink where='/review'>{Text.otherReview[language.language]}</Navlink>
                <Button>{Text.send[language.language]}</Button>
            </div>
        </form>
    )
}

export default Contact_form
