import { motion, useAnimation } from 'framer-motion'
import { observer } from 'mobx-react-lite'
import React from 'react'
import { useInView } from 'react-intersection-observer'
import theme from '../../store/theme'
import Video from '../../video/Video_component'
import styles from './TheWork.module.css'

interface TheWorkProps {
    image: string,
    technology: string,
    link: string,
    title: string,
    client: string,
    server?: string,
    path_video?: string
}

const TheWork:React.FC<TheWorkProps> = observer(({ image, technology, link, title, client, server, path_video }) => {
    let {ref, inView} = useInView() 
    let [videoOpen, setVideoOpen] = React.useState<Boolean>(false)    
    let color = theme.black ? 44 : 233
    let animation = useAnimation()

    React.useEffect(() => {
        if (inView) {
            animation.start({
                x: 0,
                opacity: 1,
                transition: {duration: 0.9}
            })
        }
    })

    return (
            <>
                <motion.div ref={ref} style={{backgroundColor: `rgb(${color}, ${color}, ${color})`}} initial={{ opacity: 0 }} animate={animation} className={styles.flex}>
                    {title === 'Интернет-магазин.' || title === 'Клон инстаграма. (Ранняя бета, видео)' ? (
                        <button style={{ outline: 'none', border: 'none', backgroundColor: 'transparent'}} onClick={() => setVideoOpen(true)}>
                            <p style={{ textAlign: 'center', margin: '12px' }}><span>{title}</span></p>
                            <img src={image}  alt="" />
                        </button>  
                    ): (
                        <a target='_blank' href={link}>
                            <p style={{ textAlign: 'center', margin: '12px' }}><span>{title}</span></p>
                            <img src={image}  alt="" />
                        </a>  
                    )}
                
                    <p className={styles.git}><span>GitHub:</span> <a target='_blank' href={client} >Клиент</a>{server && <a target='_blank' href={server}>, сервер</a>} </p>
                    {title === 'Чат.' && <p className={styles.show}><span>Показать: </span><a target='_blank' href={link}>Сайт,</a><button className={styles.button} onClick={() => setVideoOpen(true)}> видео</button></p>}
                    <p className={styles.technology}><span>Технологии и инструменты</span>: {technology}</p>
                </motion.div>  
                {videoOpen && <Video toClose={() => setVideoOpen(false)} path={path_video} />}
            </>
    )
})

export default TheWork