import Work_bar from './Work_bar'
import Work_top from './Work_top'
import styles from './Work.module.css'  
import Coronavirus from './allWorks/Coronavirus'
import Business from './allWorks/Business'
import SqrLearning from './allWorks/SqrLearning'
import Todo from './allWorks/Todo'
import allwork from './images/allwork.png'
import Pizza from './allWorks/Pizza'
import { Text } from '../../text'
import language from '../../store/language'

const All_works = () => {
    return (
        <div>
            <Work_top name={Text.allWorks[language.language]} img={allwork}/>
            <div className={styles.work__wrapper}>
                <Work_bar name={Text.layout[language.language]}>             
                    <Coronavirus />
                    <Business />
                </Work_bar>
                <Work_bar name={Text.react[language.language]}>
                    <SqrLearning />
                    <Todo />
                </Work_bar>         
                <Work_bar name={Text.fullstack[language.language]}>
                    <Pizza />
                </Work_bar>         
            </div>
        </div>
    )
}

export default All_works
