import React from 'react'
import { NavLink } from 'react-router-dom'
import styles from './Navlink.module.css'

interface NavlinkProps {
    className?: string,
    where: string
}

const Navlink:React.FC<NavlinkProps> = ({className, children, where}) => {
    return (
        <NavLink className={styles.navlink + ' ' + className} to={where}>{children}</NavLink>
    )
}

export default Navlink
