import React from 'react'
import Work_top from './Work_top'
import portfolio from './images/Screenshot_2.png'
import thiswork from './images/thiswork.png'
import SpecialLinks from './SpecialLinks'
import SpecialInfo from './SpecialInfo'
import SpecialWrapper from './SpecialWrapper'
import ButtonLink from '../reused/ButtonLink'
import config from '../../project_info'
import { Text } from '../../text'
import language from '../../store/language'

const This_work = () => {
    return (
        <div>
            <Work_top name={Text.thisSite[language.language]} img={thiswork}/> 
            <SpecialWrapper>
                <SpecialLinks size='170' photo={portfolio} >
                    <ButtonLink where={config[language.language].portfolio.client_git}>GitHub</ButtonLink>
                </SpecialLinks>
                <SpecialInfo link={config[language.language].portfolio.link_site} title={config[language.language].portfolio.short_title} description={config[language.language].portfolio.description} technology={config[language.language].portfolio.technology} />
            </SpecialWrapper>      
        </div>
    )
}

export default This_work
