import { motion, useAnimation } from 'framer-motion'
import React from 'react'
import { useInView } from 'react-intersection-observer'
import styles from './Work.module.css'

interface SpecialWrapperProps {

}

const SpecialWrapper:React.FC<SpecialWrapperProps> = ({ children }) => {
    let imgView = useInView()
    let infoView = useInView()
    let animation = useAnimation()
    let animation_2 = useAnimation()

    React.useEffect(() => {
        if (infoView.inView) {
            animation_2.start({
                x: 0,
                opacity: 1,
                transition: {duration: 0.8}
            })
        }    
    }, [infoView.inView])

    React.useEffect(() => {
        if (imgView.inView) {
            animation.start({
                x: 0,
                opacity: 1,
                transition: {duration: 0.8}
            })
        }    
    }, [imgView.inView])

    return (
        <div className={styles.this_work__wrapper}>    
            <motion.div initial={{x: 270, opacity: 0}} ref={imgView.ref} animate={animation} className={styles.ready__site}>
                {children}
            </motion.div>
        </div>
    )
}

interface SpecialWrapperProps {

}

export default SpecialWrapper
